@use 'sass:math'; @import '/Users/paulfalgout/Projects/care-ops-frontend/src/scss/provider-variables.scss';
.action--red {
  color: color(red);
  font-weight: bold;
}

.action--black {
  color: $black-20;
  font-weight: bold;
}

.action--green {
  color: color(green);
  font-weight: bold;
}

.action--orange {
  color: color(orange);
  font-weight: bold;
}

.action--purple {
  color: color(purple);
  font-weight: bold;
}

.action--grey {
  color: $black-60;
  font-weight: bold;
}

.is-highlighted {
  .action--red, .action--black, .action--green, .action--orange, .action--grey {
    color: $white;
  }
}
