@use 'sass:math'; @import '/Users/paulfalgout/Projects/care-ops-frontend/src/scss/provider-variables.scss';
.clinician-state--disabled {
  color: color(red);
  font-weight: bold;
}

.clinician-state--pending {
  color: color(orange);
  font-weight: bold;
}

.clinician-state--active {
  color: color(green);
  font-weight: bold;
}

.is-highlighted {
  .clinician-state--disabled,
  .clinician-state--pending,
  .clinician-state--active {
    color: $white;
  }
}
